import React, { useState } from "react";
import Latex from "react-latex";
import "./App.css";

const THRESHOLD = 3 * 3600 * 1000; // 12 hours threshold

const importance = (date, count) => {
  const x = (new Date() - new Date(date)) / 2 ** count;
  return Number.isNaN(x) ? 1e6 : x;
};

const backup = (filename, text) => {
  var element = document.createElement("a");
  element.setAttribute(
    "href",
    "data:text/plain;charset=utf-8," + encodeURIComponent(text)
  );
  element.setAttribute("download", filename);
  element.style.display = "none";
  document.body.appendChild(element);
  element.click();
  document.body.removeChild(element);
};

const Controls = ({ turned, turn, next, answer, cards, cardIndex }) => {
  if (!cards || cards.length === 0) return null;
  return (
    <div id="controls">
      {!turned && (
        <button className="button" onClick={turn}>
          SHOW
        </button>
      )}
      {turned && (
        <button
          className="button"
          style={{ backgroundColor: "green" }}
          onClick={() => answer(true) || next(true)}
        >
          I KNOW
        </button>
      )}
      {turned && (
        <button
          className="button"
          style={{ backgroundColor: "red" }}
          onClick={() => answer(false) || next(true)}
        >
          I FORGOT
        </button>
      )}
    </div>
  );
};

const FlashCard = ({ turned, turn, card, toggleConfig }) => {
  const [front, back] = (card && [card.front, card.back]) || ["", ""];
  //const reverse = card && new Date(card.lastSeenAt).getSeconds() % 2 > 0.5;
  const reverse = false;
  const [f, b] = reverse ? [back, front] : [front, back];
  return (
    <div id="flashcard" onClick={turn}>
      <div className={turned ? "showFront" : "showBack"}>
        <div className="front">
          <i className="edit-button fas fa-pencil-alt" onClick={toggleConfig} />
          <MemoryBattery cl="mem-battery" card={card} />
          <Latex>{f}</Latex>
        </div>
        <div className="back">
          <i className="edit-button fas fa-pencil-alt" onClick={toggleConfig} />
          <MemoryBattery cl="mem-battery" card={card} />
          <Latex>{b}</Latex>
        </div>
      </div>
    </div>
  );
};

const MemoryBattery = ({ card, cl }) => {
  if (!card) return null;
  const memoryScore = importance(card.lastSeenAt, card.count);
  var icon = "fas fa-battery-full";
  if (memoryScore > 1 * THRESHOLD) icon = "fas fa-battery-three-quarters";
  if (memoryScore > 2 * THRESHOLD) icon = "fas fa-battery-half";
  if (memoryScore > 3 * THRESHOLD) icon = "fas fa-battery-quarter";
  if (memoryScore > 4 * THRESHOLD) icon = "fas fa-battery-empty";
  return <i className={cl + " " + icon} />;
};

const Header = ({ toggleConfig, cards, setCardIndex, configOpen, next }) => {
  const toggleAdd = () => {
    if (cards.length > 0) {
      if (!configOpen) {
        setCardIndex(undefined);
      }
      if (configOpen) next(false);
      toggleConfig();
    }
  };
  return (
    <div id="header">
      <span>Flashcards</span>
      <i className="fas fa-plus" onClick={toggleAdd} />
    </div>
  );
};

const getCards = config => {
  try {
    const cards = localStorage.getItem("lpfaucon.flashcards.cards");
    return cards ? JSON.parse(cards) : [];
  } catch {
    return [];
  }
};

const ConfigPanel = ({
  toggleConfig,
  setCardIndex,
  cardIndex,
  cards,
  setCards
}) => {
  const f = cardIndex === undefined ? "" : cards[cardIndex].front;
  const b = cardIndex === undefined ? "" : cards[cardIndex].back;
  const [front, setFront] = useState(f);
  const [back, setBack] = useState(b);

  const addCard = () => {
    if (front === "" || back === "") return;
    const newCard = { front, back, lastSeenAt: new Date(), count: 0 };
    var newCards;
    if (cardIndex === undefined) {
      newCards = [...cards, newCard];
    } else {
      newCards = [...cards];
      newCards[cardIndex] = newCard;
    }
    localStorage.setItem("lpfaucon.flashcards.cards", JSON.stringify(newCards));
    toggleConfig();
    setCards(newCards);
    if (cardIndex === undefined) setCardIndex(newCards.length - 1);
  };

  const mem = Object.keys(localStorage)
    .map(k => localStorage[k].length)
    .reduce((a, b) => a + b, 0);

  return (
    <div id="config">
      {cards.length === 0 && <Instructions />}
      <span>Front:</span>
      <textarea value={front} onChange={e => setFront(e.target.value)} />
      <span>Back:</span>
      <textarea value={back} onChange={e => setBack(e.target.value)} />
      <button
        className="button"
        style={{ marginTop: "16px" }}
        onClick={addCard}
      >
        {cardIndex === undefined ? "ADD" : "SAVE"}
      </button>
      <div className="separator" />
      <p>Memory used {(mem / 512).toFixed(2)} KB</p>
      <div className="separator" />
      <span>Download a file containing all your cards</span>
      <button className="button-grey">Back-Up</button>
      <div className="separator" />
      <span>Load cards</span>
      <button className="button-grey">Load</button>
    </div>
  );
};

const Instructions = () => {
  return (
    <div>
      <span>
        Welcome to Flashcards app. To start using the app the only thing you
        need to do is adding a card by writing down below what should appear at
        the front and at the back of it. Later on you can add more cards by
        clicking the <i className="fas fa-plus" /> on top of the screen.
      </span>
      <div className="separator" />
    </div>
  );
};

const App = () => {
  const [cards, setCards] = useState(getCards());
  const [turned, setTurned] = useState(false);
  const [configOpen, setConfigOpen] = useState(!cards || cards.length === 0);

  const newCardIndex = () =>
    cards.reduce((_max, c, idx) => {
      const { lastSeenAt, count } = c;
      const { lastSeenAt: da, count: co } = cards[_max];
      const better = importance(lastSeenAt, count) > importance(da, co);
      return better ? idx : _max;
    }, 0);

  const [cardIndex, setCardIndex] = useState(newCardIndex());

  const next = withDelay => {
    setTurned(false);
    if (withDelay) {
      setCardIndex(undefined);
      setTimeout(() => setCardIndex(newCardIndex()), 350);
    } else {
      setCardIndex(newCardIndex());
    }
  };

  const handleRecall = wasCorrect => {
    const newCards = [...cards];
    if (!newCards[cardIndex]) return;
    const { lastSeenAt, count } = newCards[cardIndex];
    newCards[cardIndex].lastSeenAt = new Date();
    if (wasCorrect) {
      const im = importance(lastSeenAt, count);
      const increment = im > THRESHOLD ? 1 : im / THRESHOLD;
      newCards[cardIndex].count = (newCards[cardIndex].count || 0) + increment;
    } else {
      newCards[cardIndex].count = 0;
    }
    localStorage.setItem("lpfaucon.flashcards.cards", JSON.stringify(newCards));
    setCards(newCards);
  };

  const turn = () => setTurned(!turned);
  const toggleConfig = () => setConfigOpen(!configOpen);

  return (
    <div className="App">
      <Header
        toggleConfig={toggleConfig}
        cards={cards}
        setCardIndex={setCardIndex}
        next={next}
        configOpen={configOpen}
      />
      {!configOpen && (
        <FlashCard
          turned={turned}
          turn={turn}
          card={cards && cards[cardIndex]}
          toggleConfig={toggleConfig}
        />
      )}
      {!configOpen && (
        <Controls
          turned={turned}
          cards={cards}
          cardIndex={cardIndex}
          turn={turn}
          next={next}
          answer={handleRecall}
        />
      )}
      {configOpen && (
        <ConfigPanel
          cards={cards}
          cardIndex={cardIndex}
          setCardIndex={setCardIndex}
          toggleConfig={toggleConfig}
          setCards={setCards}
        />
      )}
    </div>
  );
};

export default App;
